<template>
  <v-card>
    <form @submit.prevent="addClient()">
      <v-card-title>
        <span class="headline">Client Registration Form </span>
      </v-card-title>
      <v-card-text>
        <v-text-field
          label="First Name"
          name="First name"
          v-model="customer.first_name"
          required
        ></v-text-field>
        <v-text-field
          label="Last Name"
          name="Last name"
          v-model="customer.last_name"
          required
        ></v-text-field>
        <v-text-field
          label="Company"
          name="Company"
          v-model="customer.company_name"
          required
        />
        <v-text-field
          label="Email"
          name="email"
          v-model="customer.email"
          required
        ></v-text-field>
        <v-text-field
          label="Phone"
          name="Phone"
          v-model="customer.phone_number"
          required
        ></v-text-field>
        <v-text-field
          label="Address"
          name="Address"
          v-model="customer.address"
          required
        ></v-text-field>
        <v-spacer></v-spacer>
        <v-btn
        color="success" @click="addClient()">
        Submit
        </v-btn>
      </v-card-text>
    </form>
   </v-card>
</template>
<script>
import axios from '@axios'
export default{
  name:"customer-add",
  data(){
    return{
      customer:{
        first_name:"",
        last_name:"",
        company_name:"",
        email:"",
        phone_number:"",
        address:""
      }
    }
  },
  async mounted(){
        const content = await axios.get('/customer/'+this.$route.params.id)
        this.customer = content.data
  },
  methods : {
      updateClient(){
        axios
          .put('/customer/', this.customer)
          .then(
            this.$router.push('/apps/client/list')
          )
          .catch(error => { console.log(error.response.data) })
        }
    }
  }

</script>
